import React, { Component } from "react";
import "./navegacion.css";
import { Link } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";

export default class Navegacion extends Component {
  render() {
    return (
      <div>
        <Navbar bg="light" expand="lg">
          <div className="container-xl">
            <Navbar.Brand href="/chinopandarin/">Chino Pandarin</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="ml-auto">
                <Nav.Link>
                  <Link to="/chino-pandarin-react-nodb/" className="link">
                    Inicio
                  </Link>
                </Nav.Link>
                <Nav.Link>
                  <Link to="/chino-pandarin-react-nodb/material" className="link">
                    Material
                  </Link>
                </Nav.Link>
                <Nav.Link>
                  <Link to="/chino-pandarin-react-nodb/contacto" className="link">
                    Contacto
                  </Link>
                </Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </div>
        </Navbar>
      </div>
    );
  }
}
